import { Button, Divider, Text } from "@geist-ui/react";
import { useState, useEffect } from "react";

const LZJCard = (props) => {
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState("");

  useEffect(() => {
    setTitle(props.title);
    setDesc(props.desc);
  }, [props.title, props.desc]);

  return (

      <div className="flex flex-col bg-white bg-opacity-50 w-full p-2 rounded">
        <Text h2>{title}</Text>
        <Divider></Divider>
        <Text className="flex-grow" h4>
          {desc}
        </Text>
        <Button className="ring pb-14 w-8">tap</Button>
      </div>

  );
};
export default LZJCard;
